@import "./../components/default/default.scss";

.gallery__gallery-grid-blocks {
  display: flex;
  flex-direction: column;
  gap: 31px;
  li {
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    gap: 32px;
    width: 100%;
    @media (max-width: $tablet) {
      flex-direction: column;
      max-height: none;
    }
    .img-block {
      width: 100%;
    }
    &:nth-child(3n + 1) {
      &.reverse {
        flex-direction: row-reverse;
        @media (max-width: $tablet) {
          flex-direction: column-reverse;
        }
      }
      div {
        &:nth-child(1) {
          flex: 0 0 57.22%;
          height: 100%;
          @media (max-width: $tablet) {
            flex: auto;
            img {
              width: 100%;
              object-fit: cover;
              max-height: 376px;
              object-position: 50% 20%;
            }
          }
        }
        &:nth-child(2) {
          flex: 0 0 41%;
          width: 100%;
          height: 100%;
          position: relative;
          img {
            height: 100%;
            width: 100%;
            max-height: 556px;
            object-fit: cover;
          }
          @media (max-width: $tablet) {
            flex: auto;
            img {
              max-height: 376px;
            }
          }
          .text-block {
            padding-top: 77px;
            padding-left: 39px;
            padding-right: 90px;
            background-color: rgba($color: $darkGreen, $alpha: 0.97);
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            img {
              max-width: 322px;
              max-height: 293px;
              position: absolute;
              right: -108px;
              bottom: -46px;
            }
            &__title {
              font-family: "Montserrat";
              font-style: normal;
              font-weight: 800;
              font-size: 22px;
              line-height: 27px;
              letter-spacing: 0.01em;
              color: #ffffff;
            }
            &__text {
              margin-top: 22px;
              font-family: "Montserrat";
              font-style: normal;
              font-weight: 500;
              font-size: 15px;
              line-height: 22px;
              color: #ffffff;
              strong {
                letter-spacing: -0.01em;
              }
            }
          }
        }
      }
    }
    &:nth-child(3n + 2) {
      div {
        &:nth-child(1) {
          width: 100%;
          height: 100%;
          @media (max-width: $tablet) {
            max-height: 376px;
          }
          img {
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
          .text-block {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .icon-play {
              margin-top: 108px;
            }
            &__title {
              max-width: 563px;
              margin-top: 28px;
              font-family: "Montserrat";
              font-style: normal;
              font-weight: 800;
              font-size: 22px;
              line-height: 27px;
              letter-spacing: 0.01em;
              color: #ffffff;
            }
            &__text {
              margin-top: 12px;
              max-width: 563px;
              font-family: "Montserrat";
              font-style: normal;
              font-weight: 500;
              font-size: 14px;
              line-height: 21px;
              text-align: center;
              color: #ffffff;
              margin-right: 5px;
            }
            strong {
              letter-spacing: -0.01em;
            }
          }
        }
      }
    }
    &:nth-child(3n + 3) {
      div {
        &:nth-child(1) {
          max-width: 409px;
          max-height: 404px;
          img {
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
          @media (max-width: $tablet) {
            max-width: none;
            max-height: 376px;
          }
        }
        &:nth-child(2) {
          max-width: 409px;
          max-height: 404px;
          img {
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
          @media (max-width: $tablet) {
            max-width: none;
          }
        }
        &:nth-child(3) {
          max-width: 409px;
          width: 100%;
          height: 100%;
          img {
            object-fit: cover;
            width: 100%;
            height: 100%;
          }
          @media (max-width: $tablet) {
            max-width: none;
            img {
              object-position: 50% 50%;
            }
          }
          .text-block {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .icon-play {
              margin-top: 22px;
            }
            &__title {
              max-width: 563px;
              margin-top: 25px;
              font-family: "Montserrat";
              font-style: normal;
              font-weight: 800;
              font-size: 22px;
              line-height: 27px;
              letter-spacing: 0.01em;
              color: #ffffff;
            }
            &__text {
              margin-top: 13px;
              max-width: 363px;
              font-family: "Montserrat";
              font-style: normal;
              font-weight: 500;
              font-size: 14px;
              line-height: 21px;
              text-align: center;
              color: #ffffff;
              margin-right: 5px;
            }
            strong {
              letter-spacing: -0.01em;
            }
          }
        }
      }
    }
  }
  .video-block {
    .icon-play {
      border: 2px solid $white;
      border-radius: 50%;
      width: 61px;
      height: 61px;
      display: flex;
      justify-content: center;
      align-items: center;
      &::before {
        margin-left: 5px;
        font-size: 20px;
        color: $white;
      }
      &:hover {
        border-color: rgba($color: #eeeeee, $alpha: 1);
        &::before {
          color: rgba($color: #eeeeee, $alpha: 1);
        }
      }
    }
    position: relative;
    max-height: 554px;
    height: 100%;
    overflow: hidden;
    .text-block {
      text-align: center;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.4);
      cursor: pointer;
      &:hover {
        background-color: rgba(0, 0, 0, 0.521);
      }
    }
  }
  .img-block-small {
    max-width: 409px;
    max-height: 404px;
    overflow: hidden;
    img {
      object-position: 50% 50%;
    }
  }
  img {
  }
  .text-block {
  }
}
.unbackground {
  margin-top: 70px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
  &:hover {
  }
  &:active {
  }
  &:focus {
  }
}
